import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/pages/index/Index'
import MyTag from '@/pages/tag/MyTag'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/tag',
      name: 'MyTag',
      component: MyTag
    }
  ]
})



