<template>
  <div class="my-nav">
    <div class="my-nav-logo">
      <a href="#">
        <img src="../../assets/logo.png" class="my-nav-logo-img"/>
      </a>
    </div>
    <el-menu class="el-menu-demo my-nav-menu" mode="horizontal">
      <el-menu-item v-for="menu in menuList" :key="menu.id" class="my-nav-item">
        <router-link :to="menu.path" class="my-nav-item-link">
          {{ menu.title }}
        </router-link>
      </el-menu-item>

      <el-submenu  class="switch-bg my-nav-item">
        <template slot="title">设置</template>
        <el-menu-item index="2-1">选项1</el-menu-item>
        <el-menu-item index="2-2">选项2</el-menu-item>
        <el-menu-item index="2-3">选项3</el-menu-item>
      </el-submenu>

    </el-menu>
    <div class="line"></div>
  </div>
</template>

<script>
export default {
  name: 'MyNav',
  data () {
    return {
      msg: '这是导航栏'
    }
  },
  props: {
    menuList: {
      type: Array,
      required: true
    }
  }
}
</script>

<style scoped>
.my-nav-logo{
  text-align: center;
  float: left;
  margin-left: 24%;
  margin-right: 8%;
}
.my-nav-logo-img{
  width: 58px;
  height: 48px;
  margin-top: 20px;
}
.my-nav-menu {
  background: none !important;
  height: 82px;
  border: none !important;
  /*left: 40%;*/
}
.my-nav-item{
  padding: 10px;
  text-align: center;
  color: white;
}
.my-nav-item-link{
  display: inline !important;
  padding: 6px 24px !important;
  text-decoration: none !important;
  box-shadow: 0 0 5px rgba(202,203,203,0.4) !important;
  color: #409eff !important;
}
.my-nav-item-link:hover{
  color: white !important;
  box-shadow: 0 0 5px rgb(202,203,203) !important;
  border: none !important;
}
.switch-bg{
  border: none !important;
  line-height: 82px;
  color: #8c939d;
}
.switch-bg:hover{
  color: rgb(254, 232, 240) !important;
  background: none !important;
}
.el-menu-item{
  background: none !important;
}
.el-menu--horizontal>.el-menu-item.is-active{
  border-bottom: none !important;
}
</style>



