        <span>卡片名称</span>
      </div>
      <div v-for="o in 4" :key="o" class="text item">
        {{'列表内容 ' + o }}
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'MessageBox'
}
</script>

<style scoped>
.Message-Box{
  margin-top: 10%;
}
.Message-Box-Item{
  width: 90%;
  margin: 10% 5%;
  border: white solid 1px;
  border-radius: 5px;
}
</style>



